import React from 'react'

import Badge from '../../../../components/badgeComponent/Badge'
import AdvantageCard from './AdvantageCard'
import Elipse from '../../../../components/elipse/Elipse'

const advantages = [
	{
		id: 1,
		title: 'Tytuł elementu',
		text: 'Prawidłowe wykonanie zielonego dachu to niezbędny element, by mogła powstać trwała i funkcjonalna przestrzeń, z której z przyjemnością będzie można korzystać przez wiele lat.',
	},
	{
		id: 2,
		title: 'Tytuł elementu',
		text: 'Zielony dach to idealne rozwiązanie, które zapobiega nadmiernemu przegrzewaniu się pomieszczeń latem, a zimą chroni je przed oziębieniem. Jest odporny na uszkodzenia mechaniczne i działanie wiatru, a dodatkowo zwiększa bezpieczeństwo pożarowe.',
	},
	{
		id: 3,
		title: 'Tytuł elementu',
		text: 'To rozwiązanie niesie ze sobą wiele korzyści, takie jak poprawa mikroklimatu bezpośredniego otoczenia – w upalne dni temperatura powietrza nad zielonym dachem jest średnio od 3 do 5°C niższa niż otoczenia oraz wyłapują do 20% zanieczyszczeń zawartych w powietrzu.',
	},
]
const GreenRoofAdvantage = () => {
	const advantagesList = advantages.map(advantage => (
		<React.Fragment>
		<AdvantageCard
			key={advantage.id}
			id={advantage.id}
			title={advantage.title}
			text={advantage.text}
		/>
		{advantages.length !== advantage.id && window.innerWidth > 640 && <div className='xl:w-[1px] xl:h-64 xl:bg-separator xl:mt-2 xl:mx-6 xl:rounded-full'></div>}
		{advantages.length !== advantage.id && window.innerWidth <= 640 && <div className=' w-full h-[1px] bg-separator rounded-full'></div>}
		</React.Fragment>
	))

	return (
		<div className='relative z-10'>
			{window.innerWidth > 1024 && <Elipse num={3} classes='top-[-280px] left-[-490px]'/>}
			<div className='mx-4 xl:mx-10 relative z-10'>
				<div className=''>
					<Badge title='Zielone dachy' />
				</div>
				<div className='mt-3 text-base xl:text-2xl font-semibold'>Jakie są zalety zielonych dachów?</div>
				<div className='mt-4 xl:mt-3 text-gray-800 text-sm xl:text-base font-normal leading-normal'>
					Dachy pokryte roślinnością zyskują na popularności jako ekologiczne rozwiązanie, które ożywia przestrzeń
					miejska, zdominowaną przez gęstą zabudowę pozbawioną zieleni.
				</div>
				<div className='xl:flex xl:justify-between mt-2 xl:mt-6'>{advantagesList}</div>
			</div>
		</div>
	)
}

export default GreenRoofAdvantage