import { useState } from 'react'
import FAQItem from './FAQItem'

const faqData = [
	{
		id: 'accordion1',
		num: 1,
		title: 'Dlaczego warto wybrać rozchodniki?',
		content:
			'Dzięki płytkiemu systemowi korzeniowemu rozchodnikowi wystarcza mniejsza warstwa podłoża. Jako roślina odporna na suszę, choroby oraz szkodniki nie wymaga wielu składników odżywczych, ani zabiegów pielęgnacyjnych.',
	},
	{
		id: 'accordion2',
		num: 2,
		title: 'Jakie są zalety zielonych dachów?',
		content:
			'Zielone dachy są nie tylko estetyczne, ale również przynoszą wiele korzyści. Dzięki nim można zaoszczędzić na kosztach ogrzewania i klimatyzacji, a także poprawić jakość powietrza w mieście.',
	},
	{
		id: 'accordion3',
		num: 3,
		title: 'Jakie są zalety zielonych dachów?',
		content:
			'Zielone dachy są nie tylko estetyczne, ale również przynoszą wiele korzyści. Dzięki nim można zaoszczędzić na kosztach ogrzewania i klimatyzacji, a także poprawić jakość powietrza w mieście.',
	},
	{
		id: 'accordion4',
		num: 4,
		title: 'Czy jest zielony dach ekstensywny?',
		content:
			'Zielone dachy są nie tylko estetyczne, ale również przynoszą wiele korzyści. Dzięki nim można zaoszczędzić na kosztach ogrzewania i klimatyzacji, a także poprawić jakość powietrza w mieście.',
	},
]

const FAQAccordion = () => {
	const [activeId, setActiveId] = useState<string | null>(null);

	const handleClick = (id: string) => {
		setActiveId(prevId => (prevId === id ? null : id));
	  };
	

	const faqList = faqData.map(item => (
		<FAQItem
			key={item.id}
			item={item}
			isActive={item.id === activeId}
			onClick={() => handleClick(item.id)}
		/>
	))

	return (
		
		<div
			id='accordion-color'
			data-accordion='collapse'
			data-active-classNamees='bg-blue-100 dark:bg-gray-800 text-blue-600 dark:text-white'>
			{faqList}
		</div>
	)
}

export default FAQAccordion
